import React,{useState} from 'react';
import Table from './tablas/Table'



class Reloj extends React.Component {
    constructor(props){
        super(props);
        this.state = {date: new Date()}
    }

    render(){
        return(
            <div>
                <h2> La hora es: {this.state.date.toLocaleTimeString()}</h2>
            </div>
        )
    }
}

const Section = (props) => {

    const [datos, setDatos] = useState('');

    function handleChange(e){
        setDatos(e.target.value);
        console.log(datos);
    }

    function handleClick(e){
    document.getElementById('boton-click').textContent = datos;

    }


    return (
        <div >
            <section className="section-parrafo">
            
            <Reloj/>
            <p >Hola  {props.user} </p>  {/* le estoy pasando un prop */}
            <Table />
            <p>El monte Fuji es el símbolo de Japón y la montaña sagrada más venerada por los japoneses. Se trata de un volcán inactivo desde 1708 que se eleva a más de 2.400 metros de altura y tiene una bonita capa de nieve coronando su cima.</p>
            <p>Se formó hace aproximadamente 100 000 años a consecuencia de erupciones volcánicas que fueron transformándolo hasta convertirlo en el pico más alto de todo Japón, alzándose a 3776 m de altitud.</p>

            

            <input type="text" value={datos} onChange={handleChange}/>
            <p id="boton-click"></p>
            <button onClick={handleClick}>click</button>




            </section>



            
        </div>
    );
};

export default Section;